import Vue from 'vue';
import Vuex from 'vuex';
import { Column } from '../models/Column';
import { Block } from '../models/Block';
import { FixedBlock } from '../models/FixedBlock';

Vue.use(Vuex);

export default new Vuex.Store({
  state: () => ({
    columns: [
      new Column({
        id: 'queue',
        title: 'Zásobník',
        effectivity: Number.POSITIVE_INFINITY,
        blocks: [
          new Block({
            id: 'block1',
            title: 'Kompletační list 1',
            work: 16,
            color: 'bg-red-300',
            blocked: ['column1'],
          }),
          new Block({
            id: 'block2',
            title: 'Kompletační list 2',
            work: 32,
            color: 'bg-red-300',
          }),
          new Block({
            id: 'block3',
            title: 'Kompletační list 3',
            work: 4,
            color: 'bg-orange-300',
            blocked: ['column1', 'column2', 'column3'],
          }),
          new Block({
            id: 'block4',
            title: 'Kompletační list 4',
            work: 8,
            color: 'bg-orange-300',
          }),
          new Block({
            id: 'block5',
            title: 'Kompletační list 5',
            work: 16,
            color: 'bg-orange-300',
          }),
          new Block({
            id: 'block6',
            title: 'Kompletační list 6',
            work: 16,
            color: 'bg-yellow-300',
          }),
          new Block({
            id: 'block7',
            title: 'Kompletační list 7',
            work: 32,
            color: 'bg-yellow-300',
          }),
          new Block({
            id: 'block8',
            title: 'Kompletační list 8',
            work: 4,
            color: 'bg-green-300',
          }),
          new Block({
            id: 'block9',
            title: 'Kompletační list 9',
            work: 8,
            color: 'bg-green-300',
            blocked: ['column3'],
          }),
          new Block({
            id: 'block10',
            title: 'Kompletační list 10',
            work: 16,
            color: 'bg-green-300',
            blocked: ['column5'],
          }),
        ],
      }),
      new Column({
        id: 'column1',
        title: 'Pracoviště 1',
        effectivity: 2,
        blocks: [],
      }),
      new Column({
        id: 'column2',
        title: 'Pracoviště 2',
        effectivity: 1,
        blocks: [
          new FixedBlock({
            id: 'fixedBlock1',
            title: 'Pravidelná oprava',
            top: 8,
            height: 16,
          }),
        ],
      }),
      new Column({
        id: 'column3',
        title: 'Pracoviště 3',
        effectivity: 0.5,
        blocks: [],
      }),
      new Column({
        id: 'column4',
        title: 'Pracoviště 4',
        effectivity: 3,
        blocks: [
          new FixedBlock({
            id: 'fixedBlock1',
            title: 'Pravidelná oprava',
            top: 4,
            height: 4,
          }),
          new FixedBlock({
            id: 'fixedBlock1',
            title: 'Pravidelná oprava',
            top: 16,
            height: 4,
          }),
        ],
      }),
      new Column({
        id: 'column5',
        title: 'Pracoviště 5',
        effectivity: 0.5,
        blocks: [],
      }),
    ],
    dragging: null,
  }),

  mutations: {
    initColumn: (state, { column }) => {
      column.recalculate();
    },

    initBlock: (state, { block, el }) => {
      block.el = el;
    },

    dragStart: (state, { block }) => {
      state.dragging = block;
      block.isDragging = true;
    },

    dragEnd: (state, { block }) => {
      state.dragging = null;
      block.isDragging = false;
    },

    dragOver: (state, { column, y }) => {
      if (!state.dragging) return;
      if (state.dragging.blocked.includes(column.id)) return;

      const getSpliceIndex = (column, y) => {
        const elements = column.blocks.map(block => block.el);

        let closestOffset = Number.NEGATIVE_INFINITY;
        let closestIndex = column.blocks.length;

        for (const [index, element] of elements.entries()) {
          const box = element.getBoundingClientRect();

          const upperFrom = box.top;
          const upperTo = upperFrom + box.height / 2;
          const lowerFrom = upperTo;
          const lowerTo = lowerFrom + box.height / 2;

          if (y > upperFrom && y < upperTo) {
            if (element === state.dragging.el) return index;

            return index;
          } else if (y > lowerFrom && y < lowerTo) {
            if (element === state.dragging.el) return index;

            return index + 1;
          } else {
            const offset = y - box.top - box.height / 2;

            if (offset < 0 && offset > closestOffset) {
              closestOffset = offset;
              closestIndex = index;
            }
          }
        }

        return closestIndex;
      };

      const getFrom = () => {
        for (const [i, _column] of state.columns.entries()) {
          for (const [j, _block] of _column.blocks.entries()) {
            if (_block !== state.dragging) continue;

            return {
              columnFrom: i,
              blockFrom: j,
            };
          }
        }
      };

      const getTo = () => {
        for (const [i, _column] of state.columns.entries()) {
          if (_column !== column) continue;

          return {
            columnTo: i,
            blockTo: getSpliceIndex(column, y),
          };
        }
      };

      let { columnFrom, blockFrom } = getFrom();
      let { columnTo, blockTo } = getTo();

      if (columnFrom === columnTo && blockFrom < blockTo) blockTo--;
      if (columnFrom === columnTo && blockFrom === blockTo) return;

      state.columns[columnTo].blocks.splice(blockTo, 0, ...state.columns[columnFrom].blocks.splice(blockFrom, 1));

      state.columns[columnFrom].recalculate();
      state.columns[columnTo].recalculate();
    },
  },
});
