<template>
  <div
    class="absolute left-0 right-0 bg-transparent p-2 pb-0 cursor-move"
    :style="`height: ${block.height}rem; top: ${block.top}rem`"
    @dragstart="onDragStart"
    @dragend="onDragEnd"
    draggable
  >
    <div class="shadow rounded p-2 h-full" :class="classes">
      {{ block.title }}
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex';
import { Block } from '../models/Block';
import { Column } from '../models/Column';

export default {
  props: {
    block: {
      type: Block,
      required: true,
    },
    column: {
      type: Column,
      required: true,
    },
  },

  mounted() {
    this.initBlock({
      block: this.block,
      el: this.$el,
    });
  },

  computed: {
    classes() {
      return [this.block.color, { 'opacity-50 border border-blue-400': this.block.isDragging }];
    },
  },

  methods: {
    ...mapMutations(['initBlock', 'dragStart', 'dragEnd']),

    onDragStart(e) {
      e.dataTransfer.setDragImage(new Image(), 0, 0);

      this.dragStart({
        block: this.block,
      });
    },

    onDragEnd() {
      this.dragEnd({
        block: this.block,
      });
    },
  },
};
</script>
